import React, { useState, useEffect, useRef } from 'react';
import { View, Text, StyleSheet, SafeAreaView, Image, Dimensions, Platform, TextInput, ScrollView, Animated, TouchableOpacity } from 'react-native';
import { useNavigation, useRoute, useFocusEffect } from '@react-navigation/native';
import NextArrowButton from './NextArrowButton';
import ScrollableGradientWrapper from '../components/ScrollableGradientWrapper';

const { width: SCREEN_WIDTH } = Dimensions.get('window');

const CutLengthsScreen = () => {
  const navigation = useNavigation();
  const route = useRoute();
  const { selectedHose, selectedHosePrice, isHoseOnly, selectedFitting, end1Shape, end1Size, end1Price, isOrderFittingMode , selectedEnd2Fitting, end2Shape, end2Size, end2Price, selectedAngle } = route.params || {};

  const [quantity, setQuantity] = useState('');
  const [error, setError] = useState('');
  const [cutLengths, setCutLengths] = useState(isHoseOnly ? [{ length: '', unit: 'mm' }] : []);
  const [showCutLengths, setShowCutLengths] = useState(isHoseOnly);
  const [allLengthsFilled, setAllLengthsFilled] = useState(false);
  const [lastInputIndex, setLastInputIndex] = useState(-1);
  const [cutLengthErrors, setCutLengthErrors] = useState([]);
  const quantityInputRef = useRef(null);
  const inputRefs = useRef([]);
  const [totalPrice, setTotalPrice] = useState(0);
  const [cutLengthPrice, setCutLengthPrice] = useState(0);
  const fadeAnim = useRef(new Animated.Value(0)).current;
  const [hasSubmitted, setHasSubmitted] = useState(false);

  const SelectedHoseDisplay = ({ size, price }) => (
    <View style={styles.selectedHoseContainer}>
      <Text style={styles.selectedHoseHeader}>Selected Hose Size</Text>
      <Text style={styles.selectedHoseSize}>{size}</Text>
      <View style={styles.selectedHosePriceContainer}>
        <Text style={styles.selectedHosePrice}>A$ {price}/meter</Text>
      </View>
    </View>
  );

  const DynamicPriceDisplay = ({ totalPrice, isHoseOnly }) => (
    <View style={styles.selectedHoseContainer}>
      <Text style={styles.selectedHoseHeader}>
        {isOrderFittingMode ? 'Total Fitting Price' : 
         isHoseOnly ? 'Hose Only Price' : 'Total Hose and Fitting Price'}
      </Text>
      <View style={styles.selectedHosePriceContainer}>
        <Text style={styles.selectedHosePrice}>A$ {totalPrice.toFixed(2)}</Text>
      </View>
    </View>
  );

  useEffect(() => {
    // Short timeout to ensure the input is rendered
    const timer = setTimeout(() => {
      if (isHoseOnly && inputRefs.current[0]) {
        inputRefs.current[0].focus();
      } else if (!isHoseOnly && quantityInputRef.current) {
        quantityInputRef.current.focus();
      }
    }, 100);
  
    return () => clearTimeout(timer);
  }, [isHoseOnly]);
  
useEffect(() => {
  if (!isHoseOnly && showCutLengths) {
    const num = parseInt(quantity, 10);
    const maxQuantity = isOrderFittingMode ? 50 : 20;  // Match the max quantity logic
    if (!isNaN(num) && num > 0 && num <= maxQuantity) {  // Updated condition
      setCutLengths(Array(num).fill({ length: '', unit: 'mm' }));
      setCutLengthErrors(Array(num).fill(''));
      inputRefs.current = inputRefs.current.slice(0, num);
      setTimeout(() => {
        if (inputRefs.current[0]) {
          inputRefs.current[0].focus();
        }
      }, 100);
    } else {
      setCutLengths([]);
      setCutLengthErrors([]);
    }
  }
}, [quantity, showCutLengths, isHoseOnly, isOrderFittingMode]);

  useEffect(() => {
    const allFilled = cutLengths.length > 0 && cutLengths.every(cut => cut.length !== '');
    
    if (cutLengths.length > 1) {
      if (lastInputIndex === cutLengths.length - 1) {
        const emptyFields = cutLengths.map((cut, index) => cut.length === '' ? index : -1)
                                    .filter(index => index !== -1);
        
        if (emptyFields.length > 0) {
          const newErrors = [...cutLengthErrors];
          emptyFields.forEach(index => {
            newErrors[index] = 'Please enter value in the empty fields';
          });
          setCutLengthErrors(newErrors);
          setAllLengthsFilled(false);
        } else {
          const allNumeric = cutLengths.every(cut => /^\d+$/.test(cut.length));
          if (allNumeric) {
            setCutLengthErrors(Array(cutLengths.length).fill(''));
          }
          setAllLengthsFilled(true && showCutLengths);
        }
      } else {
        const allNumeric = cutLengths.every(cut => /^\d+$/.test(cut.length));
        if (allNumeric) {
          setCutLengthErrors(Array(cutLengths.length).fill(''));
        }
      }
    } else {
      setAllLengthsFilled(allFilled && showCutLengths);
    }
  }, [cutLengths, showCutLengths, lastInputIndex]);

  useEffect(() => {
    Animated.timing(fadeAnim, {
      toValue: 1,
      duration: 800,
      useNativeDriver: Platform.OS === 'native',
    }).start();
  }, []);

  useFocusEffect(
    React.useCallback(() => {
      // Only reset the animation
      fadeAnim.setValue(0);
      Animated.timing(fadeAnim, {
        toValue: 1,
        duration: 800,
        useNativeDriver: Platform.OS === 'native',
      }).start();
  
      // We'll keep this to ensure the keyboard appears when needed
      if (!quantity && quantityInputRef.current) {
        const timer = setTimeout(() => {
          quantityInputRef.current.focus();
        }, 100);
        return () => clearTimeout(timer);
      }
    }, [fadeAnim])
  );

  const validateQuantity = (value) => {
    // Reset states for both modes
    setShowCutLengths(false);
    setHasSubmitted(false);
    
    if (value === '') {
      setQuantity(value);
      setTotalPrice(0);
      setError(''); // Clear any existing errors
      return;
    }
    
    // Immediately show error for non-numeric input
    if (!/^\d+$/.test(value)) {
      setError('Please enter numbers only');
      setTotalPrice(0);
      return;
    }
    
    // If we get here, we have a valid number
    const num = parseInt(value, 10);
    setError(''); // Clear any existing errors
    setQuantity(value);
    
    // For Fitting Mode: Calculate price immediately
    if (isOrderFittingMode) {
      const fittingPrice = parseFloat(end1Price || 0);
      const totalPrice = fittingPrice * num;
      setTotalPrice(totalPrice);
      setCutLengthPrice(totalPrice);
    }
  };
  
  const handleQuantitySubmit = () => {
    // Validate quantity limits on submit
    const num = parseInt(quantity, 10);
    const maxQuantity = isOrderFittingMode ? 50 : 20;
  
    if (quantity === '') {
      setError('Please enter a quantity');
      return;
    }
    
    // Add minimum quantity validation
    if (num < 1) {
      setError('Minimum quantity is 1');
      return;
    }
    
    if (num > maxQuantity) {
      setError(`Maximum quantity is ${isOrderFittingMode ? '50' : '20'}`);
      return;
    }
  
    // If we get here, there are no errors
    setError('');
  
    // Proceed based on mode
    if (isOrderFittingMode) {
      setHasSubmitted(true);
      calculatePrice([]); // Ensure price is calculated
    } else {
      setShowCutLengths(true);
      setTimeout(() => {
        if (inputRefs.current[0]) {
          inputRefs.current[0].focus();
        }
      }, 100);
    }
  };

  const updateCutLength = (index, length) => {
    const updatedLengths = [...cutLengths];
    const updatedErrors = [...cutLengthErrors];
  
    // Update the length regardless of content
    updatedLengths[index] = { ...updatedLengths[index], length };
  
    // Special handling for Hose Only mode
    if (isHoseOnly) {
      // Only check numeric validation
      if (!/^\d+$/.test(length) && length !== '') {
        updatedErrors[index] = 'Please enter numbers only';
      } else if (hasSubmitted) {
        // Only show minimum length error if user has submitted before
        const lengthValue = parseInt(length, 10);
        if (lengthValue < 50) {
          updatedErrors[index] = 'Minimum length is 50mm';
        } else {
          updatedErrors[index] = '';
        }
      } else {
        updatedErrors[index] = '';
      }
      
      setCutLengths(updatedLengths);
      setCutLengthErrors(updatedErrors);
      calculatePrice(updatedLengths);
      return;
    }
  
    // Existing code for other modes
    if (index === cutLengths.length - 1) {
      setHasSubmitted(false);
    } else {
      calculatePrice(updatedLengths);
    }
    
    if (!/^\d+$/.test(length) && length !== '') {
      updatedErrors[index] = 'Please enter numbers only';
    } else if (length === '' && index === cutLengths.length - 1) {
      updatedErrors[index] = 'Please enter value in the empty fields';
    } else if (/^\d+$/.test(length)) {
      updatedErrors[index] = '';
    }
  
    setCutLengths(updatedLengths);
    setCutLengthErrors(updatedErrors);
  };

const handleNext = () => {
  // Animation config to reuse
  const animationConfig = {
    toValue: 0,
    duration: 800,
    useNativeDriver: Platform.OS === 'native',
  };

  // Fitting Only mode
  if (isOrderFittingMode && quantity !== '' && !error && hasSubmitted) {
    Animated.timing(fadeAnim, animationConfig).start(() => {
      navigation.navigate('OrderConfirmation1', {
        quantity: parseInt(quantity, 10),
        selectedFitting: route.params.selectedFitting,
        end1Shape: route.params.end1Shape,
        end1Size: route.params.end1Size,
        end1Price: route.params.end1Price,
        cutLengthPrice,
        selectedHose: null,
        selectedHosePrice: null,
        selectedEnd2Fitting: null,
        end2Shape: null,
        end2Size: null,
        end2Price: null,
        selectedAngle: null,
        isHoseOnly: false,
        isOrderFittingMode: true
      });
    });
  } 
  // Hose Only mode
  else if (isHoseOnly && cutLengths[0].length && !cutLengthErrors[0] && hasSubmitted) {
    Animated.timing(fadeAnim, animationConfig).start(() => {
      navigation.navigate('HoseProtection', {
        cutLengths,
        cutLengthPrice,
        selectedHose,
        selectedHosePrice,
        selectedFitting,
        end1Shape,
        end1Size,
        end1Price,
        selectedEnd2Fitting,
        end2Shape,
        end2Size,
        end2Price,
        selectedAngle,
        isHoseOnly: true
      });
    });
  }
  // Normal mode
  else if (!isOrderFittingMode && !isHoseOnly && allLengthsFilled && hasSubmitted) {
    Animated.timing(fadeAnim, animationConfig).start(() => {
      navigation.navigate('HoseProtection', {
        quantity: parseInt(quantity, 10),
        cutLengths,
        cutLengthPrice,
        selectedHose,
        selectedHosePrice,
        selectedFitting,
        end1Shape,
        end1Size,
        end1Price,
        selectedEnd2Fitting,
        end2Shape,
        end2Size,
        end2Price,
        selectedAngle,
        isHoseOnly: false
      });
    });
  }
};

const calculatePrice = (lengths) => {
  if (isOrderFittingMode) {
    // For fitting mode, price is simply end1Price * quantity
    const fittingPrice = parseFloat(end1Price || 0);
    const qty = parseInt(quantity, 10);
    const totalPrice = fittingPrice * qty;
    
    setTotalPrice(totalPrice);
    setCutLengthPrice(totalPrice);
  } else {
    // Original price calculation for hose mode
    const totalLengthInMeters = lengths.reduce((sum, cut) => {
      const lengthInMillimeters = parseInt(cut.length || '0', 10);
      return sum + (lengthInMillimeters / 1000);
    }, 0);
    
    const pricePerMeter = parseFloat(selectedHosePrice);
    const hosePrice = totalLengthInMeters * pricePerMeter;
    
    const calculatedPrice = isHoseOnly 
      ? hosePrice 
      : hosePrice + parseFloat(end1Price || 0) + parseFloat(end2Price || 0);
    
    setTotalPrice(calculatedPrice);
    setCutLengthPrice(calculatedPrice);
  }
};

  const handleInputSubmit = (index) => {
    const currentValue = cutLengths[index].length;
    
    // Special handling for Hose Only mode
    if (isHoseOnly) {
      if (!currentValue || !/^\d+$/.test(currentValue)) {
        return;
      }
      
      const length = parseInt(currentValue, 10);
      if (length < 50) {
        setCutLengthErrors(['Minimum length is 50mm']);
      } else {
        setCutLengthErrors(['']);
      }
      setHasSubmitted(true);
      calculatePrice(cutLengths);
      return;
    }

    // Existing code for other modes
    if (!currentValue || !/^\d+$/.test(currentValue)) {
      return;
    }

    if (index < cutLengths.length - 1) {
      inputRefs.current[index + 1].focus();
      calculatePrice(cutLengths);
    } else {
      setHasSubmitted(true);
      calculatePrice(cutLengths);
    }
    
    const allFilled = cutLengths.every(cut => cut.length !== '' && /^\d+$/.test(cut.length));
    setAllLengthsFilled(allFilled);
    
    setLastInputIndex(index);
  };

  const handleKeyPress = (e, index) => {
    if (e.nativeEvent.key === 'Tab') {
      e.preventDefault();
      handleInputSubmit(index);
    }
  };

  const renderCutLengthRows = () => {
    if (!showCutLengths) return null;
  
    if (isHoseOnly) {
      // Single cut length input for Hose Only mode
      return (
        <View>
          <View style={styles.cutLengthRow}>
            <View style={styles.cutLengthInputContainer}>
              <TextInput
                ref={el => inputRefs.current[0] = el}
                style={[
                  styles.cutLengthInput,
                  cutLengthErrors[0] ? styles.inputError : null
                ]}
                value={cutLengths[0].length}
                placeholder="Enter length in millimeters"
                placeholderTextColor="#999999"
                onChangeText={(text) => updateCutLength(0, text)}
                onSubmitEditing={() => handleInputSubmit(0)}
                keyboardType="numeric"
                returnKeyType="done"
                enterKeyHint="done"  // Add this
                inputMode="numeric"  // Add this
                autoComplete="off"   // Add this
              />
              <Text style={styles.unitText}>mm</Text>
            </View>
          </View>
          {cutLengthErrors[0] ? <Text style={styles.errorText}>{cutLengthErrors[0]}</Text> : null}
        </View>
      );
    }
  
    // Original multiple cut lengths rendering for normal mode
    return (
      <View>
        {cutLengths.map((cut, index) => (
          <View key={index}>
            <View style={styles.cutLengthRow}>
              <View style={styles.rowLabelContainer}>
                <View style={styles.rowNumberContainer}>
                  <Text style={styles.rowNumber}>{index + 1}.</Text>
                </View>
                <Text style={styles.cutLengthText}>Cut Length</Text>
              </View>
              <View style={styles.cutLengthInputContainer}>
                <TextInput
                  ref={el => inputRefs.current[index] = el}
                  style={[
                    styles.cutLengthInput,
                    cutLengthErrors[index] ? styles.inputError : null
                  ]}
                  value={cut.length}
                  placeholder="Enter length in millimeters"
                  placeholderTextColor="#999999"
                  onChangeText={(text) => updateCutLength(index, text)}
                  onSubmitEditing={() => handleInputSubmit(index)}
                  onKeyPress={(e) => handleKeyPress(e, index)}
                  keyboardType="numeric"
                  returnKeyType={index === cutLengths.length - 1 ? "done" : "next"}
                  enterKeyHint={index === cutLengths.length - 1 ? "done" : "next"}  // Add this
                  inputMode="numeric"  // Add this
                  autoComplete="off"   // Add this
                  blurOnSubmit={false}
                />
                <Text style={styles.unitText}>{cut.unit}</Text>
              </View>
            </View>
            {cutLengthErrors[index] ? <Text style={styles.errorText}>{cutLengthErrors[index]}</Text> : null}
          </View>
        ))}
      </View>
    );
  };

return (
    <View style={styles.mainContainer}> 
      <TouchableOpacity 
        style={styles.floatingBackButton}
        onPress={() => navigation.goBack()}
      >
        <Text style={styles.backButtonText}>←</Text>
      </TouchableOpacity>
      <ScrollableGradientWrapper>
        <SafeAreaView style={styles.container}>
          <View style={styles.logoContainer}>
            <Image
              source={require('../assets/fluidpower_logo.gif')}
              style={styles.logo}
              resizeMode="contain"
            />
          </View>
  
          <Animated.View style={[styles.animatedContainer, { opacity: fadeAnim }]}>
            <ScrollView contentContainerStyle={styles.scrollContent}>
              <View style={styles.contentContainer}>
                {selectedHose && selectedHosePrice && (
                  <SelectedHoseDisplay 
                    size={selectedHose.size}
                    price={selectedHosePrice}
                  />
                )}
  
                {!isHoseOnly && (
                  <>
                    <View style={styles.titleContainer}>
                      <Text style={styles.titleText}>No. of Hoses REQUIRED</Text>
                    </View>
                    <View style={styles.quantityRow}>
                      <View style={styles.quantityContainer}>
                        <Text style={styles.titleText}>QTY</Text>
                      </View>
                      <TextInput
                        ref={quantityInputRef}
                        style={styles.quantityInput}
                        value={quantity}
                        onChangeText={validateQuantity}
                        onSubmitEditing={handleQuantitySubmit}
                        keyboardType="numeric"
                        maxLength={2}
                        returnKeyType="done"
                        enterKeyHint="done"  // Add this
                        inputMode="numeric"  // Add this
                        autoComplete="off"
                      />
                    </View>
                    {/* Add error message display here */}
                    {error ? (
                      <View style={styles.errorContainer}>
                        <Text style={styles.errorText}>{error}</Text>
                      </View>
                    ) : null}
                  </>
                )}
                
                <View style={styles.subtitleContainer}>
                  <Text style={styles.titleText}>
                    {isHoseOnly ? 'Hose Cut LENGTH' : 'Hose Cut LENGTHS'}
                  </Text>
                </View>
                
                <Image
                  source={require('../assets/hose_cut_length.png')}
                  style={styles.diagram}
                  resizeMode="contain"
                />
                
                <View style={styles.cutLengthsContainer}>
                  {renderCutLengthRows()}
                </View>
  
                {totalPrice > 0 && (
                  <View style={styles.pricingContainer}>
                    <DynamicPriceDisplay 
                      totalPrice={totalPrice}
                      isHoseOnly={isHoseOnly}
                    />
                  </View>
                )}
              </View>
            </ScrollView>
          </Animated.View>
        </SafeAreaView>
      </ScrollableGradientWrapper>
  
      {/* Next Button Footer */}
        {((isOrderFittingMode && quantity !== '' && !error && hasSubmitted) || // Fitting Only mode
          (isHoseOnly && cutLengths[0].length && 
          /^\d+$/.test(cutLengths[0].length) && 
          parseInt(cutLengths[0].length) >= 50) || // Modified Hose Only mode condition
          (!isOrderFittingMode && !isHoseOnly && allLengthsFilled && hasSubmitted && // Normal mode
          cutLengthErrors.every(error => error === '') && 
          cutLengths.every(cut => /^\d+$/.test(cut.length)))) && (
          <View style={styles.viewportFooter}>
            <Animated.View style={[styles.footerContent, { opacity: fadeAnim }]}>
              <View style={styles.footerRight}>
                <NextArrowButton onPress={handleNext} isActive={true} />
              </View>
            </Animated.View>
          </View>
        )}
    </View>
  );
};

