    position: 'absolute',
    bottom: Platform.OS === 'ios' ? 40 : 20,
    right: 20,
    backgroundColor: 'rgba(255, 255, 255, 0.9)', // semi-transparent white background
    paddingVertical: 10,
    paddingHorizontal: 10,
    borderRadius: 20,
    shadowColor: '#000',
